<template>
  <div class="download">
    <crumb></crumb>
    <div class="table">
      <el-table
        :data="data"
        style="width: 100%">
        <el-table-column
          label="认证文件">
          <template slot-scope="scope">
            <el-button type="text">{{scope.row.title}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'download',
  data() {
    return {
      data: [
        {
          title: '工程教育认证办法.docx'
        },
        {
          title: '专业类认证委员会管理办法.docx'
        },
        {
          title: '工程教育认证新增认证专业（专业类）管理规定.docx'
        },
        {
          title: '工程教育认证专家管理办法.docx'
        }
      ]
    }
  },
}
</script>

<style lang="scss">
.download {
  .table {
    background-color: #fff;
    border-radius: 10px;
    padding-bottom: 50px;
  }
}
</style>


